import { Api } from './base/api';
export class LarekApi extends Api {
    constructor(baseUrl, cdn, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    getProduct(id) {
        return this.get(`/product/${id}`).then((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image.replace('.svg', '.png') })));
    }
    getProductList() {
        return this.get('/product').then((response) => response.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image.replace('.svg', '.png') }))));
    }
    placeOrder(order) {
        return this.post('/order', order).then((response) => response);
    }
}
