import { Component } from '../base/Component';
import { createElement, ensureElement, formatNumber } from '../../utils/utils';
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._items = ensureElement('.basket__list', this.container);
        this._total = ensureElement('.basket__price', this.container);
        this._button = ensureElement('.basket__button', this.container);
        if (this._button) {
            this._button.addEventListener('click', () => {
                events.emit('order:open');
            });
        }
        this.total = 0;
        this.items = [];
    }
    set items(items) {
        if (items.length) {
            this._items.replaceChildren(...items);
        }
        else {
            this._items.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста',
            }));
        }
    }
    set total(total) {
        this.setText(this._total, formatNumber(total) + ' синапсов');
    }
    set toggleButton(value) {
        this.setDisabled(this._button, value);
    }
}
