import { Form } from "./Form";
import { ensureElement } from "../../utils/utils";
export class OrderForm extends Form {
    constructor(container, events) {
        super(container, events);
        this._typeCash = ensureElement('button[name="cash"]', this.container);
        this._typeCard = ensureElement('button[name="card"]', this.container);
        this._typeCash.addEventListener('click', () => {
            this.onInputChange('payment', 'cash');
            this.payment = 'offline';
        });
        this._typeCard.addEventListener('click', () => {
            this.onInputChange('payment', 'card');
            this.payment = 'online';
        });
    }
    set address(value) {
        this.container.elements.namedItem('address').value = value;
    }
    set payment(value) {
        this.toggleClass(this._typeCard, 'button_alt-active', value === 'online');
        this.toggleClass(this._typeCash, 'button_alt-active', value === 'offline');
    }
}
