import { ensureElement } from "../../utils/utils";
import { Component } from "../base/Component";
export class Card extends Component {
    constructor(blockName, container, actions) {
        super(container);
        this.blockName = blockName;
        this._title = ensureElement(`.${blockName}__title`, this.container);
        this._image = container.querySelector(`.${blockName}__image`);
        this._category = container.querySelector(`.${blockName}__category`);
        this._description = container.querySelector(`.${blockName}__description`);
        this._price = ensureElement(`.${blockName}__price`, this.container);
        this._button = container.querySelector(`.${blockName}__button`);
        this._index = container.querySelector('.basket__item-index');
        // Если в катрочке нет кнопки, слушатель вешается на саму карточку
        if (actions === null || actions === void 0 ? void 0 : actions.onClick) {
            if (this._button) {
                this._button.addEventListener('click', actions.onClick);
            }
            else {
                container.addEventListener('click', actions.onClick);
            }
        }
    }
    set id(value) {
        this.container.dataset.id = value;
    }
    get id() {
        return this.container.dataset.id || '';
    }
    set title(value) {
        this.setText(this._title, value);
    }
    get title() {
        return this._title.textContent || '';
    }
    set image(value) {
        this.setImage(this._image, value, this.title);
    }
    set description(value) {
        this.setText(this._description, value);
    }
    set price(value) {
        this.setText(this._price, value ? `${value.toString()} синапсов` : 'Бесценно');
        this.setDisabled(this._button, value === null);
    }
    set category(value) {
        this.setText(this._category, value);
    }
    set categoryClass(value) {
        this.toggleClass(this._category, `card__category_${value}`);
    }
    set available(value) {
        this.setText(this._button, value ? 'Удалить' : 'Купить');
    }
    set index(value) {
        this.setText(this._index, value);
    }
}
