import { Model } from "../base/Model";
export class CatalogItem extends Model {
    get categoryClass() {
        const categoryClass = new Map([
            ['софт-скил', 'soft'],
            ['хард-скил', 'hard'],
            ['другое', 'other'],
            ['дополнительное', 'additional'],
            ['кнопка', 'button'],
        ]);
        return categoryClass.get(this.category);
    }
}
